.main{
    width: 100%;
    height: 100vh;
    background-color: #282c34;
    display: flex;
    align-items: center;
    justify-content: center;
}

.join-box{
    background-color: #ffffff;
    width: 400px;
    height: 400px;
    border-radius: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 30px;
}

.join-box label{
    font-size: 22px;
    font-weight: 700;
    margin-bottom: 25px;
}

.join-box input{
    padding: 8px 10px;
    width: 100%;
    background: transparent;
    outline: none;
    color: #282c34;
    font-size: 16px;
    border: none;
    border-bottom: 1px solid #282c34;
}

.join-box button{
    margin-top: 25px;
    padding: 12px 30px;
    font-size: 18px;
    background-color: #3db6ee;
    border: none;
    outline: none;
    border-radius: 10px;
    color: white;
    cursor: pointer;
}

.video-box{
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}